import React from 'react';
import ReactDOM from 'react-dom';
import Typist from 'react-typist';
import './index.css';

var recents = [];
var str = "Last 10 numbers appear here...";
var ind = "0"
var pickedNumbers = Array(91).fill(false);
function Square(props) {
  let cssClass = "";
  if (props.isChosen) {
    cssClass += "selected-square";
  }
  else {
    cssClass += "square";
  }
  return (
    <button id={"number-" + props.value} className={`${cssClass}`} onClick={props.onClick}>
      {props.value}
    </button>
  );
}

class Board extends React.Component {
  constructor(props) {
    super(props);
    const N = 90;
    this.state = {
      squares: Array.from(Array(N), (_, index) => index + 1),
      isChosen: Array(N).fill(false),
      renderRef: props.renderRef,
    }
  }

  renderSquare(i, isChosen) {
    return (
      <Square value={this.state.squares[i]}
        onClick={() => this.handleClick(i)}
        isChosen={isChosen}
      />
    );
  }

  handleClick(i) {
    const isChosen = this.state.isChosen;
    isChosen[i] = !isChosen[i];
    this.setState({
      isChosen: isChosen,
    });
    if (isChosen[i]) {
      pickedNumbers[i+1] = true;
      recents.push(i + 1)
    } else {
      recents = recents.filter(e => e != (i + 1));
      pickedNumbers[i+1] = false;
    }
    this.state.renderRef();
  }

  createBoard(row, col) {
    const board = [];
    let cellCounter = 0;

    for (let i = 0; i < row; i += 1) {
      const row = [];
      for (let j = 0; j < col; j += 1) {
        row.push(this.renderSquare(cellCounter++, this.state.isChosen[cellCounter - 1]));
      }
      board.push(<div key={i} className="board-row">{row}</div>);
    }

    return board;
  }

  render() {
    return <div>{this.createBoard(9, 10)}</div>;
  }
}

class Game extends React.Component {
  constructor(props) {
    super(props);
    this.names = ["Yuvrej", "V. Lexingten", "Sehway", "Tendehar", "Dhenier"];
    this.state = {
      currentIndex: "0",
    }
  }

  setRecentNumbers() {
    var recentValue = "";
    var LENGTH_TO_DISPLAY = 10;
    if (recents.length <= LENGTH_TO_DISPLAY) {
      for (var i in recents) {
        if (i > 0) recentValue = recents[i] + " &nbsp&nbsp&nbsp&nbsp " + recentValue;
        else recentValue = recents[i];
      }
    } else {
      var len = recents.length;
      for (var i = len - 1; i >= len - LENGTH_TO_DISPLAY; i--) {
        if (i == len - 1) recentValue += ("  " + recents[i]);
        else recentValue += (" &nbsp&nbsp&nbsp&nbsp " + recents[i]);
      }
    }
    document.getElementById("recent-num").innerHTML = recentValue;
  }

  incrementCurrentIndex() {
    document.getElementById("next-number-input").value = "";
    const currentIndex = String(Number(this.state.currentIndex) + 1);
    this.hideVerificationResults();
    this.setState({
      currentIndex: currentIndex,
    })
  }

  hideVerificationResults() {
    document.getElementById("verification-success").setAttribute("hidden", true);
    document.getElementById("verification-failure").setAttribute("hidden", true);
  }

  verifyAndClick() {
    this.hideVerificationResults();
    var numberVal = Number(document.getElementById("next-number-input").value);
    if(numberVal && numberVal <= 90 && numberVal >= 1) {
      if (pickedNumbers[numberVal]){
        var audioElement = document.createElement('audio');
        audioElement.setAttribute('src', '/mario_death.mp3');
        audioElement.play();
        document.getElementById("verification-failure").removeAttribute("hidden");
      }
      else {
        var audioElement = document.createElement('audio');
        audioElement.setAttribute('src', '/success_notification.mp3');
        audioElement.play();
        document.getElementById("number-"+numberVal).click();
        document.getElementById("verification-success").removeAttribute("hidden");
        // document.getElementById("next-name-button").click();
      }
    }
  }
  

  render() {
    console.log('Inside Render' + this.state.currentIndex);

    return (
      <div className="game">
        <div >
          <h1 className="page-heading py-5 text-center">
            Welcome IN Consumer Finance Team to The Great Indian Lockdown Tambola!!!
          </h1>
        </div>
        <div className="row">
          <div className="game-board col-6 pl-5">
            <Board renderRef={() => this.setRecentNumbers()}
            />
          </div>
          <div className="col-6 accessories-column">
            <div>
              <img className="event-image" src="./Banner.jpeg" />
            </div>
            <div id="recent-num" className="text-box">
              {str}
            </div>
            <div>
              <div id="name" className="text-box">
                  <div id="name-text">
                    {this.names[this.state.currentIndex]} 
                  </div>
                <div className="next-name">
                  <img id="next-name-button" className="next-arrow"
                    onClick={() => this.incrementCurrentIndex()}
                    src="next_arrow.png"
                  />
                </div>
              </div>
            </div>
            <div id="recent-num" className="text-box">
              The next number is...
              <input className="number-input ml-2" id="next-number-input" />
              <button id="verify-button" className="verify-button" onClick={() => this.verifyAndClick()}>
                Verify?
              </button>
                <span hidden id="verification-success" className="glyphicon glyphicon-ok right-symbol"></span>
                <span hidden id="verification-failure" className="glyphicon glyphicon-plus wrong-symbol"></span>
            </div>
          </div>
        </div>
        <div className="footnote mt-4">
          Made with <span className="heart">‚ù§</span> by Harsh (modihar@)
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <Game />,
  document.getElementById('root')
);
